import React, { Component, lazy, Suspense } from "react";
import Skeleton from "react-loading-skeleton";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import Axios from "axios";
import { reduxForm, reset } from "redux-form";
import { getUUID } from "../../actions/fakturAction";
import PrintNota from "../../Stocking/Component/PrintNota";
import { warning } from "../../Alert";

const TitipanRupiah = lazy(() => import("../Element/TitipanRurpiah"));
const maptoState = (state) => {
  return {
    totalCiok: state.provinsi.totalCiok,
    totalRongsok: state.provinsi.totalRongsok,
    totalBrutoCiok: state.provinsi.totalBrutoCiok,
    totalBrutoRongsok: state.provinsi.totalBrutoRongsok,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
  };
};
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class FormTitipanSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
  }
  handleSubmitGr(data) {
    var bayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf"));
    var bayarTunai = JSON.parse(localStorage.getItem("alatBayarTunai"));
    var split = data.supplier ? data.supplier : "DEFAULT | DEFUALT";
    var supplier = split.split("|");
    var isi = {
      alatBayarBarang: null,
      alatBayarTunai: bayarTunai,
      alatBayarTrf: bayarTrf,
      alatBayarGiro: null,
      totalFakutrTrx: null,
      keterangan: "-",
      totalAlatBayar: {
        totalRsk: this.props.totalRongsok,
        totalCt: this.props.totalCiok,
        totalTunaiRp:
          bayarTunai !== null ? parseFloat(bayarTunai[0].tunaiNetto) : 0,
        totalTrfRp:
          bayarTrf !== null ? parseFloat(bayarTrf[0].transferNetto) : 0,
        totalPutusGr: 0,
        tipeData: "-",
        totalPutusRp: 0,
      },
      kodeSupplier: supplier[0],
      namaSupplier: supplier[1],
      noBon: this.props.noFaktur,
      noFaktur: this.props.noFaktur,
      tanggal: data.tanggal,
      tipeTransaksi: data.kriteria,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(isi);
    // INISIALISASI AUTOTABLE
    const tableRows = [supplier[1], "BARANG", "0", data.totalBayar];
    console.log(tableRows);
    var columTable = [
      "Kode Supplier",
      "Jenis Titipan",
      "Jumlah Titipan Rp",
      "Jumlah Titipan Gr",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(server + "/spp/dc/ttp", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.clear())
          .then(() => this.props.dispatch(reset("TitipanRupiah")))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "No Titip",
              this.props.noFaktur,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "NOTA TITIP SUPPLIER",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: err,
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  handleSubmitRp(data) {
    if (data.supplier === undefined) {
      warning("Mohon Pilih Supplier");
      return false;
    } else if (data.tanggal === undefined) {
      warning("Mohon Pilih tanggal");
      return false;
    } else if (data.kriteria === undefined) {
      warning("Mohon Pilih Kriteria");
      return false;
    }
    var bayarBarang = JSON.parse(localStorage.getItem("alatBayarBarang"));
    var bayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf"));
    var bayarTunai = JSON.parse(localStorage.getItem("alatBayarTunai"));
    var split = data.supplier ? data.supplier : "DEFAULT | DEFUALT";
    var supplier = split.split("|");
    var isi = {
      alatBayarBarang: bayarBarang,
      alatBayarTunai: bayarTunai,
      alatBayarTrf: bayarTrf,
      alatBayarGiro: null,
      totalFakutrTrx: null,
      keterangan: "-",
      totalAlatBayar: {
        totalRsk: this.props.totalRongsok,
        totalCt: this.props.totalCiok,
        totalTunaiRp:
          bayarTunai !== null ? parseFloat(bayarTunai[0].tunaiNetto) : 0,
        totalTrfRp:
          bayarTrf !== null ? parseFloat(bayarTrf[0].transferNetto) : 0,
        totalPutusGr: 0,
        tipeData: "-",
        totalPutusRp: 0,
      },
      kodeSupplier: supplier[0],
      namaSupplier: supplier[1],
      noBon: data.noBon,
      noFaktur: this.props.noFaktur,
      tanggal: data.tanggal,
      tipeTransaksi: data.kriteria,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(isi);
    // INISIALISASI AUTOTABLE
    const tableRows = [supplier[1], "BARANG", "0", data.totalBayar];
    console.log(tableRows);
    var columTable = [
      "Kode Supplier",
      "Jenis Titipan",
      "Jumlah Titipan Rp",
      "Jumlah Titipan Gr",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(server + "/spp/dc/ttp", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.clear())
          .then(() => this.props.dispatch(reset("TitipanRupiah")))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "No Titip",
              this.props.noFaktur,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "NOTA TITIP SUPPLIER",
              tableRows
            )
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: err,
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <ul className="nav nav-pills bg-nav-pills nav-justified mb-3">
          <li className="nav-item">
            <a
              href="#rupiah"
              data-toggle="tab"
              aria-expanded="false"
              className="nav-link rounded-0 active"
            >
              <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
              <span className="d-none d-lg-block">Gram</span>
            </a>
          </li>
          <li className="nav-item">
            <a
              href="#gram"
              data-toggle="tab"
              aria-expanded="true"
              className="nav-link rounded-0 "
            >
              <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
              <span className="d-none d-lg-block">Rupiah</span>
            </a>
          </li>
        </ul>

        <div className="tab-content">
          <div className="tab-pane show active" id="rupiah">
            <Suspense fallback={<Skeleton width={1134} height={200} />}>
              <TitipanRupiah
                from="rupiah"
                noFaktur={this.props.noFaktur}
                totalCiok={this.props.totalCiok}
                totalRongsok={this.props.totalRongsok}
                totalBrutoCiok={this.props.totalBrutoCiok}
                totalBrutoRongsok={this.props.totalBrutoRongsok}
                onSubmit={(data) => this.handleSubmitRp(data)}
              />
            </Suspense>
          </div>
          <div className="tab-pane" id="gram">
            <Suspense fallback={<Skeleton width={1134} height={200} />}>
              <TitipanRupiah
                from="gram"
                noFaktur={this.props.noFaktur}
                totalCiok={this.props.totalCiok}
                totalRongsok={this.props.totalRongsok}
                totalBrutoCiok={this.props.totalBrutoCiok}
                totalBrutoRongsok={this.props.totalBrutoRongsok}
                onSubmit={(data) => this.handleSubmitGr(data)}
              />
            </Suspense>
          </div>
        </div>
      </form>
    );
  }
}
FormTitipanSupplier = reduxForm({
  form: "FormTitipanSupplier",
  enableReinitialize: true,
})(FormTitipanSupplier);
export default connect(maptoState, null)(FormTitipanSupplier);
